<template>
  <div class="container py-5">
    <form @submit.stop.prevent="handleSubmit">
      <div class="form-group">
        <label for="name">Name</label>
        <input
          type="text"
          class="form-control"
          name="name"
          id="name"
          v-model="user.name"
          placeholder="Enter name"
        />
      </div>
      <div class="form-group">
        <label for="image">Image</label>
        <img
          :src="user.image|emptyImage"
          alt
          class="d-block img-thumbnail mb-3"
          width="200"
          height="200"
        />
        <input
          type="file"
          class="form-control-file"
          id="image"
          name="image"
          accept="image/*"
          @change="handleFileChange"
        />
      </div>
      <button
        type="submit"
        class="btn btn-primary"
        :disabled="isProcessing"
      >{{isProcessing?'sending...':'Submit'}}</button>
    </form>
  </div>
</template>

<script>
import { emptyImageFilter } from "./../utils/mixins";
import { mapState } from "vuex";
import usersAPI from "../apis/users";
import { Toast } from "../utils/helpers";
export default {
  mixins: [emptyImageFilter],
  data() {
    return {
      user: {
        name: "",
        image: ""
      },
      isProcessing: false
    };
  },
  computed: {
    ...mapState(["currentUser"])
  },
  methods: {
    handleFileChange(e) {
      console.log("e.target.files", e.target.files);
      const { files } = e.target;
      if (files.length === 0) {
        this.user.image = "";
      } else {
        const imageURL = window.URL.createObjectURL(files[0]);
        this.user.image = imageURL;
      }
    },
    async handleSubmit(e) {
      try {
        if (!this.user.name) {
          throw new Error("使用者名稱為必填");
        }
        this.isProcessing = true;
        const form = e.target;
        const formData = new FormData(form);
        // for (let [name, value] of formData.entries()) {
        //   console.log(name + ": " + value);
        // }

        const { data } = await usersAPI.update({
          formData,
          userId: this.currentUser.id
        });
        if (data.status !== "success") {
          throw new Error(data.message);
        }
        this.$router.push({ name: "user", params: this.currentUser.id });
      } catch (error) {
        this.isProcessing = false;
        Toast.fire({
          icon: "error",
          title: error.message
        });
      }
    },
    setUser() {
      //TODO: 透過API取得user info
      const { name, email, image } = this.currentUser;
      this.user = {
        ...this.user,
        name,
        email,
        image
      };
    }
  },
  watch: {
    currentUser(newValue) {
      const { id: userId } = this.$route.params;
      if (Number(userId) !== newValue.id) {
        return this.$router.push({ path: "/404" });
      }
      // console.log("userId", typeof userId);
      // console.log("newValue", typeof newValue.id);
      this.setUser();
    }
  },
  created() {
    this.setUser();
  }
};
</script>